import React, { Component } from 'react';
import { Card, CardText } from 'material-ui/Card';

class Home extends Component {
	constructor() {
		super();

    this.state = {
      data: [],
    };
	}

  componentDidMount() {
    fetch('feed.php')
      .then(response => {
        console.log(response);
        return response.blob();
      })
      .then(data => {
        console.log(data);
        this.setState({data: data});
      })
  }

	render() {
		return (
      <div className="Home">
			  <div id="feed" className="Feed">
          { this.state.data
            .filter(value => value.hasOwnProperty('message'))
              .map((object, i) =>
              <section key={i}>
                <Card className="FeedCard">
                  <CardText>
                  { object.message.split("\n")
                    .map((item, j) => 
                      <div key={j}>
                        {item}
                        <br />
                      </div>
                    )}
                  </CardText>
                </Card>
              </section>
			      )
			    }
			  </div>
		  </div>
		);
	}
}

export default Home;



