import React, { Component } from 'react';
import { Card, CardTitle, CardText } from 'material-ui/Card';

const churches = [
	{
		name: 'Agape Chinese Alliance Church',
		link: 'www.agapecac.org',
		address: '6736 Linda Vista Road, San Diego, CA 92111',
		services: [
			'English Service: 9:30 AM',
			'Cantonese Service: 11:00 AM',
		],
		contact: 'Jason Lu - (760) 580-6564',
	},
	{
		name: 'Flood Church',
		link: 'www.diveintoflood.com',
		address: 'Kearny High School 7651 Wellington Way San Diego, CA 9211',
		services: [
			'9:00 AM',
			'11:00 AM',
			'7:00 PM',
		],
  },	
  {
		name: 'Kairos',
		link: 'www.kairoschurch.org',
		address: 'On UCSD campus',
		services: [
    ],
    contact: 'Kaitlin Cheng - (562) 413-8887',
	},
]

class ChurchRides extends Component {
	render() {
		return (
      <div className="ChurchRides">
        <section>
          <Card>
            <CardTitle title="Church Rides" subtitle="Local churches around the area of San Diego" />
            <CardText>
              Since AACF is not affiliated with a church, we highly encourage you to be a part of a church while you are in San Diego!
              If you would like your church to added onto this list, or if your church information is currently incorrect, please send us an e-mail at aacf.ucsd@gmail.com.
            </CardText>
          </Card>
        </section>
        { churches.map((object, i) => 
            <section key={i}>
              <Card>
                <CardTitle title={object.name} subtitle={object.address} />
                <CardText>
                  <a href={'//' + object.link}>{object.link}</a><br/>
                  { !object.services.length
                      ? null
                      : <div>Services: <br />
                          { object.services.map((services, j) => 
                              <span key={j}>
                                {services}
                                <br />
                              </span>
                          )}
                        </div> }
                  <br />
                  { object.contact ? 
                      <span>Contact: {object.contact}</span> :
                      null
                  }
                </CardText>
              </Card>
            </section>
					)}
			</div>
		);
	}
}

export default ChurchRides;



